@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "PT Mono", "Courier", monospace;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 769px) {
  .container {
    padding: 0 25px;
  }
}

.page-header {
  position: fixed;
  padding: 0 50px;
  padding-top: 30px;
  padding-bottom: 35px;
  width: 100%;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 770px) {
  .page-header {
    padding-top: 20px;
    padding-right: 30px;
    padding-bottom: 25px;
    padding-left: 30px;
  }
}
@media (max-width: 374px) {
  .page-header {
    padding-top: 15px;
    padding-right: 10px;
    padding-bottom: 20px;
    padding-left: 10px;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.page-header__logo img {
  width: 215px;
  height: 55px;
}
@media (max-width: 769px) {
  .page-header__logo img {
    width: 35px;
    height: 35px;
  }
}

.page-header__contact {
  display: flex;
  flex-wrap: wrap;
  padding-top: 9px;
  column-gap: 126px;
  row-gap: 15px;
  margin-left: auto;
}
@media (max-width: 1040px) {
  .page-header__contact {
    margin-right: 12px;
    margin-left: 10px;
    column-gap: 40px;
  }
}
@media (max-width: 374px) {
  .page-header__contact {
    margin-right: 2px;
    padding-top: 5px;
    column-gap: 38px;
  }
}

.page-header__contact p {
  margin: 0;
  padding: 0;
  line-height: 18px;
  color: #f2f5f6;
}
.page-header__contact p:hover, .page-header__contact p:focus {
  opacity: 0.7;
}
@media (max-width: 770px) {
  .page-header__contact p {
    display: none;
  }
}

.page-header__email a,
.page-header__phone a {
  display: flex;
  column-gap: 10px;
}

.page-header__email:hover,
.page-header__phone:hover,
.page-header__email:focus-within,
.page-header__phone:focus-within {
  opacity: 0.7;
  fill-opacity: 0.7;
}

.page-header__button {
  display: block;
  margin-top: 4px;
  margin-left: auto;
  margin-right: 44px;
  padding-top: 11px;
  padding-bottom: 12px;
  width: 160px;
  font-style: 14px;
  line-height: 16px;
  letter-spacing: 0.05px;
  color: #f2f5f6;
  background-color: transparent;
  border: 1px solid #f2f5f6;
}
@media (max-width: 770px) {
  .page-header__button {
    display: none;
  }
}

.button {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  border-radius: 3px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .button:hover, .button:focus {
    opacity: 0.8;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.service {
  background-color: #fff;
}

.service ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.accordion {
  position: relative;
}

.accordion__toggle {
  display: none;
}
@media (max-width: 769px) {
  .accordion__toggle {
    position: relative;
    display: block;
    top: 0px;
    left: 3px;
    width: 20px;
    height: 20px;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
  .accordion__toggle::before, .accordion__toggle::after {
    content: "";
    position: absolute;
    top: 11px;
    right: 4px;
    width: 15px;
    height: 1px;
    background-color: #c4c4c4;
  }
  .accordion__toggle::after {
    transform: rotate(90deg);
  }
  .accordion__toggle--active::after {
    transform: rotate(0deg);
  }
  .accordion__toggle--nojs::after {
    transform: rotate(0deg);
  }
}

@media (max-width: 769px) {
  .accordion__content {
    display: none;
  }
}
.accordion__content--show {
  display: block;
}
.accordion__content--nojs {
  display: block;
}

.page-footer {
  padding-top: 50px;
  padding-bottom: 55px;
  background-color: #041827;
}
@media (max-width: 769px) {
  .page-footer {
    padding-top: 35px;
  }
}

.page-footer__wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 1023px) {
  .page-footer__wrapper {
    grid-template-columns: 1fr;
    grid-template-areas: "l l" "n a";
  }
}
@media (max-width: 769px) {
  .page-footer__wrapper {
    grid-template-areas: "l" "n" "a";
  }
}

@media (max-width: 1023px) {
  .page-footer__logo-social {
    display: flex;
    justify-content: space-between;
    grid-area: l;
  }
}
@media (max-width: 769px) {
  .page-footer__logo-social {
    position: relative;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15px;
    padding-bottom: 30px;
  }
  .page-footer__logo-social::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100vw;
    height: 1px;
    background-color: #f8fdff;
    opacity: 0.7;
  }
}

.page-footer__logo {
  margin-bottom: 35px;
  width: 215px;
  height: 55px;
}
@media (max-width: 1023px) {
  .page-footer__logo {
    margin-bottom: 30px;
  }
}
@media (max-width: 769px) {
  .page-footer__logo {
    margin-bottom: 10px;
  }
}

.page-footer__social-wrapper ul {
  margin: 0;
  margin-left: 15px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  column-gap: 25px;
  list-style: none;
}
@media (max-width: 1023px) {
  .page-footer__social-wrapper ul {
    margin-top: 10px;
    margin-right: 13px;
  }
}

.page-footer__social-wrapper a {
  display: inline-block;
  width: 30px;
  height: 30px;
  vertical-align: top;
}

.page-footer__social-wrapper a:hover,
.page-footer__social-wrapper a:focus {
  fill-opacity: 0.7;
}

.page-footer__nav {
  width: 470px;
}
@media (max-width: 1023px) {
  .page-footer__nav {
    width: 220px;
    grid-area: n;
  }
}
@media (max-width: 769px) {
  .page-footer__nav {
    position: relative;
    margin-bottom: 15px;
    width: 100%;
  }
  .page-footer__nav::after {
    content: "";
    position: absolute;
    left: -25px;
    bottom: 0;
    width: 100vw;
    height: 1px;
    background-color: #f8fdff;
    opacity: 0.7;
  }
}

@media (max-width: 769px) {
  .page-footer__nav-header,
.page-footer__address-header {
    display: flex;
    justify-content: space-between;
  }
}

.page-footer__nav h3 {
  margin: 0;
  margin-top: 7px;
  margin-bottom: 23px;
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.7px;
  color: #ffffff;
  text-transform: uppercase;
}

.page-footer__nav-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .page-footer__nav-wrapper {
    justify-content: start;
  }
}

.page-footer__nav-wrapper ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-footer__nav-wrapper li {
  margin-bottom: 9px;
  margin-right: 11px;
  width: 210px;
}

.page-footer__nav-wrapper a {
  color: #ffffff;
  opacity: 0.7;
}
.page-footer__nav-wrapper a:hover, .page-footer__nav-wrapper a:focus {
  opacity: 1;
}

.page-footer__address {
  margin-left: auto;
  max-width: 345px;
}
@media (max-width: 1023px) {
  .page-footer__address {
    padding-left: 12px;
    grid-area: a;
  }
}
@media (max-width: 769px) {
  .page-footer__address {
    position: relative;
    margin-left: 0;
    padding-left: 0;
    padding-bottom: 8px;
    max-width: 100%;
  }
  .page-footer__address::after {
    content: "";
    position: absolute;
    left: -25px;
    bottom: 0;
    width: 100vw;
    height: 1px;
    background-color: #f8fdff;
    opacity: 0.7;
  }
}

.page-footer__address h3 {
  margin: 0;
  margin-top: 7px;
  margin-bottom: 23px;
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.7px;
  color: #ffffff;
  text-transform: uppercase;
}
@media (max-width: 769px) {
  .page-footer__address h3 {
    margin-bottom: 17px;
  }
}

.page-footer__address p,
.page-footer__address a {
  margin-bottom: 9px;
  font-style: normal;
  color: #ffffff;
  opacity: 0.7;
}
@media (max-width: 769px) {
  .page-footer__address p,
.page-footer__address a {
    font-size: 14px;
  }
}

@media (max-width: 769px) {
  .page-footer__address p {
    margin-bottom: 20px;
    line-height: 18px;
  }
}

.page-footer__address a {
  display: block;
}
.page-footer__address a:hover, .page-footer__address a:focus {
  opacity: 1;
}

